.slider{
    display:block;
    position: absolute;
    width: 101%;
    bottom:0;
    left: -1%;
    height:6px;
    background: rgba(60, 60, 60, .6);
    animation-timing-function: linear;
    animation-name: slidein;
}

@keyframes slidein {
    from {
        bottom: 98%;
    }
    to {
        bottom: 0;
    }
}