.slider{
    display:block;
    position: absolute;
    width: 101%;
    bottom:0;
    left: -1%;
    height:6px;
    background: rgba(60, 60, 60, .6);
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
    -webkit-animation-name: slidein;
            animation-name: slidein;
}

@-webkit-keyframes slidein {
    from {
        bottom: 98%;
    }
    to {
        bottom: 0;
    }
}

